        var result = '';

        var paramsMessage = '<div> <p>- ' + localizer.getText('REPORTS_CONSTANTS_REPORT_MESSAGE_SELECT_FILTER_VALUE') + '</p>';
        paramsMessage += buildListFilter(paramFilters) + '</div>';
        result += paramsMessage;

        var requiedMessage = '<div> <p>- ' + localizer.getText('REPORTS_CONSTANTS_REPORT_MESSAGE_SELECT_REQUIRE_AND_NOT_VISIBLE_FIELD') + '</p>';
