!function () {
  var ensure = require('ensure');
  var mergeRouteConfig = require('logic/data/Izenda.RouteConstants').mergeRouteConfig;

  /**
  * Option object
  */
  var synergyOptions = {
    WebApiUrl: '',
    BaseUrl: '/',
    RootPath: '',
    CssFile: '',
    Integration: true, // Customer frontend app should ignore this setting (good for them)
    Routes: {
      "Settings": "settings",
      "ReportDesigner": "reportdesigner",
      "Dashboard": "dashboard",
      "Report": "report",
      "ReportViewer": "reportviewer",
      "Viewer": "viewer",
      "Account": "account",
      "MyProfile": "myprofile"
    },
    Timeout: 3600,
    PrintingTimeout: 60*1000,
    PrintingRenderingTimeout: 6*1000,
    DefaultLanguage: 'en-US',
    NeedToEncodeUrl: true, // This option is set in integration mode only. Default is true -> always encode url,
    OnReceiveUnauthorizedResponse: (message) => { }, //In integration mode, when a request receives 401 status code -> this method is run

    /**
    * Set options
    */
    set: function (options) {
      ensure(options, 'Invalid options');
      ensure(options.WebApiUrl, 'WebApiUrl must be set');

      this.WebApiUrl = options.WebApiUrl;
      this.Routes = _.merge(this.Routes, options.Routes);
      this.RoutesMerge = mergeRouteConfig(this.Routes);
      this.BaseUrl = options.BaseUrl || this.BaseUrl;
      this.RootPath = options.RootPath || this.RootPath;
      this.CssFile = options.CssFile || this.CssFile;
      this.Integration = _.isNil(options.Integration) ? this.Integration : options.Integration;
      this.TimeOut = options.TimeOut || this.Timeout;
      this.PrintingTimeout = options.PrintingTimeout || this.PrintingTimeout;
      this.NeedToEncodeUrl = _.isNil(options.NeedToEncodeUrl) ? this.NeedToEncodeUrl : options.NeedToEncodeUrl;
      this.OnReceiveUnauthorizedResponse = !_.isFunction(options.OnReceiveUnauthorizedResponse) ? this.OnReceiveUnauthorizedResponse : options.OnReceiveUnauthorizedResponse;
    },

    getConfig: function () {
      return this;
    }
  };

  module.exports = synergyOptions;
}();



